/* --- SCSS Variables --- */
// Color Palette
$primary-color: #00385E;
$secondary-color: #DDEBF3;
$hover-color: #A4C8E1;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

// Layout & UI Colors
$app-background-color: #f8f9fa;
$container-background-color: #ffffff;
$text-color-primary: #212529;
$text-color-secondary: #495057;
$border-color: #dee2e6;

// Layout & Spacing
$border-radius: 8px;
$base-padding: 1.5rem;
$grid-gap: 1.5rem;
$base-margin: 1rem;
$base-shadow: 0 5px 15px rgba(0, 0, 0, 0.07);

/* --- Main Content Area --- */
.main-content {
    flex-grow: 1;
    padding: $grid-gap;
    overflow-y: auto;
}

/* --- Locations Page Container --- */
.locations-container {
    background-color: $container-background-color;
    border-radius: $border-radius;
    padding: $base-padding;
    border: 1px solid rgba($border-color, 0.5);
}

/* --- Page Header --- */
.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $grid-gap;
    padding-bottom: $base-padding;
    border-bottom: 1px solid $border-color;

    .page-title {
        font-size: 1.75rem;
        font-weight: 600;
        color: darken($text-color-secondary, 20%);
    }
}

/* --- Filter Section --- */
.filter-section {
    margin-bottom: $grid-gap;
    padding: $base-margin;
    background-color: lighten($secondary-color, 5%);
    border-radius: $border-radius;
}

.filter-controls {
    display: flex;
    gap: $base-margin;
    align-items: center;
    flex-wrap: wrap;

    select, input[type="text"] {
        padding: 0.5rem 0.75rem;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        background-color: $container-background-color;
        min-width: 150px;

        &:focus {
            outline: none;
            border-color: $primary-color;
            box-shadow: 0 0 0 2px rgba($primary-color, 0.2);
        }
    }
}

/* --- Locations Grid --- */
.locations-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: $grid-gap;
}

.location-card {
    background-color: $container-background-color;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    padding: $base-padding;
    box-shadow: $base-shadow;
    transition: all 0.2s ease-in-out;

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
    }
}

.location-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: $base-margin;

    .location-code {
        font-size: 1.25rem;
        font-weight: 600;
        color: $text-color-primary;
        margin: 0;
    }
}

.location-status {
    .status-badge {
        display: inline-flex;
        align-items: center;
        gap: 0.25rem;
        padding: 0.25rem 0.5rem;
        border-radius: $border-radius;
        font-size: 0.8rem;
        font-weight: 500;

        &.status-occupied {
            background-color: lighten($success-color, 40%);
            color: darken($success-color, 10%);
        }

        &.status-empty {
            background-color: lighten($text-color-secondary, 50%);
            color: $text-color-secondary;
        }
    }
}

.location-details {
    margin-bottom: $base-padding;
}

.detail-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.5rem;

    .material-symbols-outlined {
        font-size: 1.2rem;
        color: $text-color-secondary;
    }

    .detail-content {
        display: flex;
        flex-direction: column;
        gap: 0.1rem;

        .detail-label {
            font-size: 0.85rem;
            color: $text-color-secondary;
            font-weight: 500;
        }

        .detail-value {
            font-size: 0.95rem;
            color: $text-color-primary;
            font-weight: 600;
        }
    }
}

.location-actions {
    display: flex;
    gap: 0.5rem;
    justify-content: flex-end;

    .btn {
        font-size: 0.85rem;
        padding: 0.4rem 0.8rem;
    }
}

/* --- Button Styles --- */
.btn {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.6rem 1.2rem;
    font-size: 0.95rem;
    font-weight: 500;
    border-radius: $border-radius;
    border: 1px solid transparent;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    white-space: nowrap;

    .material-symbols-outlined {
        font-size: 1.25rem;
    }

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    }
}

.btn-primary {
    background-color: $primary-color;
    color: #fff;
    &:hover { background-color: darken($primary-color, 8%); }
}

.btn-secondary {
    background-color: $text-color-secondary;
    color: #fff;
    &:hover { background-color: darken($text-color-secondary, 8%); }
}

.btn-danger {
    background-color: $danger-color;
    color: #fff;
    &:hover { background-color: darken($danger-color, 8%); }
}

/* --- Modal Styles --- */
.modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.2s ease-in-out;
}

.modal-content {
    background-color: $container-background-color;
    margin: 5% auto;
    padding: 0;
    border-radius: $border-radius;
    width: 90%;
    max-width: 500px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    animation: slideIn 0.3s ease-in-out;

    &.modal-small {
        max-width: 400px;
    }
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $base-padding;
    border-bottom: 1px solid $border-color;

    .modal-title {
        margin: 0;
        font-size: 1.25rem;
        font-weight: 600;
        color: $text-color-primary;
    }

    .close {
        background: none;
        border: none;
        font-size: 1.5rem;
        cursor: pointer;
        color: $text-color-secondary;
        padding: 0;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 0.2s ease-in-out;

        &:hover {
            background-color: lighten($danger-color, 40%);
            color: $danger-color;
        }
    }
}

.modal-body {
    padding: $base-padding;

    p {
        margin-bottom: $base-margin;
        line-height: 1.5;
    }
}

/* --- Form Styles --- */
.form-group {
    margin-bottom: $base-margin;

    .form-label {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        margin-bottom: 0.5rem;
        font-weight: 500;
        color: $text-color-primary;

        .material-symbols-outlined {
            font-size: 1.1rem;
            color: $text-color-secondary;
        }
    }

    .form-input, .form-textarea {
        width: 100%;
        padding: 0.75rem;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        font-size: 0.95rem;
        transition: all 0.2s ease-in-out;

        &:focus {
            outline: none;
            border-color: $primary-color;
            box-shadow: 0 0 0 3px rgba($primary-color, 0.1);
        }

        &::placeholder {
            color: lighten($text-color-secondary, 20%);
        }
    }

    .form-help {
        margin-top: 0.25rem;
        font-size: 0.8rem;
        color: $text-color-secondary;
        font-style: italic;
    }
}

.form-actions {
    display: flex;
    gap: $base-margin;
    justify-content: flex-end;
    padding: $base-padding;
    border-top: 1px solid $border-color;
    background-color: lighten($secondary-color, 8%);
    margin: 0;
}

/* --- Message Styles --- */
.message {
    padding: $base-margin;
    margin-bottom: $grid-gap;
    border-radius: $border-radius;
    border: 1px solid;
    font-weight: 500;

    &.success {
        background-color: lighten($success-color, 40%);
        border-color: darken(lighten($success-color, 40%), 10%);
        color: darken($success-color, 10%);
    }

    &.error {
        background-color: lighten($danger-color, 30%);
        border-color: darken(lighten($danger-color, 30%), 10%);
        color: darken($danger-color, 10%);
    }
}

/* --- Empty State --- */
.empty-state {
    text-align: center;
    padding: 3rem;
    color: $text-color-secondary;

    .material-symbols-outlined {
        font-size: 4rem;
        margin-bottom: $base-margin;
        opacity: 0.6;
    }

    h3 {
        margin-bottom: 0.5rem;
        color: $text-color-primary;
    }

    p {
        font-size: 0.95rem;
    }
}

/* --- Animations --- */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideIn {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

/* --- Responsive Design --- */
@media (max-width: 768px) {
    .locations-grid {
        grid-template-columns: 1fr;
    }

    .filter-controls {
        flex-direction: column;
        align-items: stretch;

        select, input[type="text"] {
            min-width: auto;
        }
    }

    .page-header {
        flex-direction: column;
        gap: $base-margin;
        align-items: stretch;
    }

    .location-actions {
        justify-content: center;
    }
}